import {Component, Input, OnInit} from '@angular/core';
import {PopupContext} from "../../../../Classes/Classes";
import {AppContextService} from "../../../../services/app-context.service";
import {DatabaseService} from "../../../../services/database.service";

@Component({
  selector: 'app-no-answer-popup',
  templateUrl: './no-answer-popup.component.html',
  styleUrls: ['./no-answer-popup.component.css']
})
export class NoAnswerPopupComponent implements OnInit {
    
    public contactName;
    @Input() public context : PopupContext;
    buttons = [
	{tip : 'Ok', icon : 'check_circle', color : '#00902e', listener : this.buttonListener.bind(this)}
    ];
    constructor(
	public appContext : AppContextService,
	public database : DatabaseService) { }
    
    
    buttonListener(){
	this.context.listener && this.context.listener();
	this.onClose();
    }
    
    onErrorContactImg(event){
	this.database.onErrorContactImg(event, this.context).then(res => {
	    console.log('Запрос изображения контакта.')
	}) ;
	return true;
    }
    
    ngOnInit(): void {
	this.contactName = this.context.contact.name || this.context.contact.displayName || this.context.contact.phoneNumber || this.context.contact.email;
	//Запуск таймера удаления уведомления, на случай, если пользователь оставит его без внимание,
	//оно должно быть удалено авоматически.
	if(this.context.extra && this.context.extra.timeout) {
	    let timeId = window.setTimeout(()=>{
		window.clearTimeout(timeId);
		this.buttonListener();
	    }, this.appContext.dialogDelay[parseInt(window.localStorage.getItem('dialogDelay'))]);//диапазон отображения диалогов
	}
    }
    
    onClose(){
	//Снятие активности контекста
	this.context.active = false;
	//Поиск индекса контекста в коллекции входящих вызовов
	let index = this.appContext.popups.value.findIndex(incoming => incoming.uid === this.context.uid);
	//Удаление входящего вызова из коллекции
	if(index > -1){
	    this.appContext.setPopups({type : false, popup : undefined, index : index});
	}
	//Обновление интерфейса
	this.appContext.appChangeRef.detectChanges();
    }
}
